.to-do-container {
   width: clamp(0px, 100%, 700px);
   margin: 0 auto;
   padding: 30px;
}
.to-do-container h1 {
  text-align: center;
}
label, input, button {
  display: block;
}
label {
  font-weight: 700;
}
input {
  width: 400px;
  border: 2px solid #434343;
  outline: 0;
  background: #fff;
  height: 35px;
  border-radius: 5px;
}
button {
  background: #666777;
  color: #fff;
  border: 2px solid #666777;
  border-radius: 15px;
  transition: 0.5s all;
  padding: 5px 15px;
  cursor: pointer;
}
button:hover {
  border: 2px solid #777666;
  background: transparent;
  color: #777666;
}
.to-do-wrapper-col {
  padding: 10px;
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em, rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
  flex: 1 1 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.todo-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
.to-do-row {
  flex: 1 1 100%;
}
p.to-do-name {
  margin: 5px;
  flex: 0 0 73%;
}
